import styled, { css } from "styled-components";
import { Title1, Title2 } from "../../assets/styles/component";
import { LazyLoadImage } from "react-lazy-load-image-component";

export const SIntro = styled.section`
  margin-top: 200px;
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;

  @media (max-width: 1000px) {
    margin-top: 75px;
  }

  // .SIntro > p

  & > p {
    margin-bottom: 100px;
    text-align: center;
  }
`;

export const Title = styled(Title1)`
  text-align: right;
  width: fit-content;
  display: block;
  margin: 0 auto;
  margin-bottom: 45px;
`;

export const BlueBG = styled.span`
  background: radial-gradient(
      81.57% 98.97% at -3.3% -14.6%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #0202f1;
  box-shadow: inset 0px 0px 43px rgba(255, 255, 255, 0.25);
  border-radius: 63.5px;
  padding: 1px 45px 6px 44px;

  @media (max-width: 1000px) {
    padding: 1px 25px 6px 25px;
  }
`;

export const PinkBG = styled.span`
  position: relative;
  padding: 0px 46px 10px 30px;
  margin: 0 20px 0 30px;
  border-radius: 103px;
  border-top-left-radius: 0;
  background: radial-gradient(
      123.91% 117.38% at 111.7% 118.95%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #cf2e82;
  box-shadow: inset 0px 0px 43px rgba(255, 255, 255, 0.25);

  svg {
    position: absolute;
    top: -39px;
    left: 0;
  }

  @media (max-width: 1000px) {
    padding: 0px 35px 10px 25px;
    margin: 0 0 0 30px;

    svg {
      width: 50px;
      top: -35px;
    }
  }
`;

export const FrontImg = styled.img`
  z-index: 1;
  width: 100%;
  height: 100%;
`;

export const BackImg = styled.img`
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
`;

export const FrontImgLazy = styled(LazyLoadImage)`
  z-index: 1;
  width: 100%;
  height: 100%;
`;

export const BackImgLazy = styled(LazyLoadImage)`
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
`;

export const FrameBoxContainer = styled.div`
  position: relative;
  overflow: visible;

  &::after {
    content: "";
    position: absolute;
    top: -30%;
    left: -70%;
    width: 230%;
    height: 145%;
    z-index: -1;

    @media (max-width: 1300px) {
      width: 200%;
      left: -60%;
    }

    background: radial-gradient(
        25.27% 36.6% at 50% 50%,
        rgba(241, 2, 117, 0.81) 0%,
        rgba(241, 2, 117, 0) 100%
      ),
      radial-gradient(
        24.63% 36.71% at 37.85% 36.14%,
        #0202f1 0%,
        rgba(2, 2, 241, 0) 100%
      ),
      radial-gradient(
        19.47% 28.35% at 55.07% 60.96%,
        #ff429e 0%,
        rgba(255, 66, 158, 0) 100%
      ),
      radial-gradient(
        21.4% 32.49% at 68.89% 47.03%,
        #cb1d85 0%,
        rgba(203, 29, 133, 0) 100%
      );
  }

  // .FrameBox:nth-child(3)

  &:nth-child(3) > div {
    & > div:first-child {
      transform: translate(-16%) rotate(-14deg);

      @media (max-width: 1300px) {
        transform: translate(-28%) rotate(-14deg) scale(0.8);
      }

      @media (max-width: 1000px) {
        transform: translate(-28%) rotate(-14deg) scale(0.7);
      }
    }

    // .promo:last-child

    & > div:last-child {
      bottom: 20%;
      right: -20%;

      @media (max-width: 1300px) {
        transform: translate(12%) rotate(14deg) scale(0.7);
      }

      @media (max-width: 1000px) {
        transform: translate(12%) rotate(14deg) scale(0.6);
      }
    }

    & > div:nth-child(2) {
      // .Frame:nth-child(2) > img:first-child
      & > img:first-child {
        transition: 0.5s ease-in-out;
        ${({ show = false }) => show && ` opacity: 0; `}
      }
    }
  }

  // .FrameBox:nth-child(6)

  &:nth-child(6) > div {
    // .FrameBox:nth-child(6) > .BlurCard:first-child

    & > div:first-child {
      transform: translate(-15%, 75%) rotate(8.9deg) scale(1.1);

      @media (max-width: 1400px) {
        transform: translate(-15%, 45%) rotate(8.9deg) scale(0.8);
      }

      @media (max-width: 1000px) {
        transform: translate(-15%, 25%) rotate(8.9deg) scale(0.7);
      }
    }

    // .FrameBox:nth-child(6) > .promo:last-child

    & > div:last-child {
      bottom: 12%;
      right: 5%;
      transform: rotate(8.9deg);

      @media (max-width: 1300px) {
        transform: translate(22%) rotate(14deg) scale(0.7);
      }

      @media (max-width: 1000px) {
        transform: translate(22%) rotate(14deg) scale(0.6);
      }
    }

    // .FrameBox:nth-child(6) > .Frame:nth-child(6) > img:first-child

    & > div:nth-child(6) {
      & > img:first-child {
        transition: 0.5s ease-in-out;
        ${({ show2 = false }) => show2 && ` opacity: 0; `}
      }
    }
  }
`;

export const FrameBox = styled.div`
  position: relative;
  margin-bottom: 170px;
  width: 62vw;
  max-width: 1000px;

  @media (max-width: 1200px) {
    margin-bottom: 80px;
  }
`;

export const Frame = styled.div`
  position: relative;
  max-width: 999px;
  min-height: 30vw;
  outline: 20px solid #ffffff22;
  border-radius: 70px;
  margin: 0 auto;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  // .Frame > img

  & > img {
    width: 100%;
    border-radius: 69px;
    margin-bottom: -4px;
  }

  @media (max-width: 1000px) {
    border-radius: 24px;
    outline: 10px solid #ffffff22;

    & > img {
      border-radius: 23px;
    }
  }
`;

export const Promo = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 15px 20px;
  border-radius: 32px;
  background: rgba(217, 217, 217, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.19);
  backdrop-filter: blur(23px);
  z-index: 5;

  right: 0;
  bottom: 0;

  // .Promo small

  small {
    font-size: 12px;
    color: #ffffff;
    opacity: 0.43;
  }

  // .Promo img

  img {
    transition: 0.25s ease-in-out;
    cursor: pointer;

    // .Promo img:hover

    &:hover {
      transform: scale(1.02);
    }
  }
`;

export const BlurCard = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 15px 20px;
  border-radius: 25px;
  background: rgba(217, 217, 217, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.19);
  backdrop-filter: blur(23px);
  z-index: 5;

  top: 12%;
  left: -17%;

  @media (max-width: 1200px) {
    top: 5%;
  }

  // .BlurCard small

  small {
    font-size: 12px;
    color: #ffffff;
    opacity: 0.43;
  }
`;

export const SubTitle = styled(Title2)`
  margin-bottom: 31px;
`;

export const PlayBtn = styled.img`
  position: absolute;
  bottom: 10%;
  left: -21%;
  width: 15vw;
  max-width: 190px;
  min-width: 53px;
  transform: rotate(-19deg);
`;

export const FrameText = styled.div`
  position: absolute;
  background: #ffffff;
  border-radius: 46px;
  transform: rotate(-5.98deg);
  padding: 63px 58px 24px 31px;
  max-width: 376px;
  min-height: 224px;
  height: fit-content;
  z-index: 2;
  box-sizing: border-box;

  bottom: -5%;
  left: -15%;

  // .FrameText img

  img {
    position: absolute;
    top: -45%;
    left: -5%;
  }

  // .FrameText p

  p {
    letter-spacing: 0.01em;
    color: #0f0f0f;
    font-size: 20px;
  }

  @media (max-width: 1600px) {
    transform: scale(0.9) rotate(-5.98deg);
  }

  @media (max-width: 1460px) {
    transform: scale(0.9) rotate(-5.98deg);
    bottom: -15%;
    left: -15%;
  }

  @media (max-width: 1200px) {
    transform: scale(0.5) rotate(-5.98deg);
    bottom: -10%;
    left: -25%;
  }

  @media (max-width: 1000px) {
    transform: scale(0.43) rotate(-5.98deg);
    bottom: -18%;
    left: -25%;
  }
`;

export const Tag = styled.div`
  position: absolute;
  z-index: 2;
  padding: 10px 29px 9px 18px;
  border-radius: 50px;
  box-shadow: inset 0px 0px 25.3184px rgba(255, 255, 255, 0.25);
  color: #fff;

  // .Tag:nth-child(4)

  &:nth-child(4) {
    border-top-right-radius: 0;
    bottom: -45px;
    right: 10%;
    background: #ffa011;

    // .Tag:nth-child(4) svg

    svg {
      position: absolute;
      top: 0;
      right: 0;
      transform: translateY(-100%);
    }
  }

  // .Tag:nth-child(3)

  &:nth-child(3) {
    border-top-left-radius: 0;
    top: 12%;
    right: 0;
    background: #4354f1;
    transform: translateX(50%) rotate(-9.72deg);

    // .Tag:nth-child(3) svg

    svg {
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(-100%);
    }
  }
`;

export const Arrow = styled.img`
  position: absolute;
  top: 32%;
  left: -21%;

  @media (max-width: 1450px) {
    top: 35%;
    left: -18%;
    transform: scale(0.9);
  }

  @media (max-width: 1300px) {
    top: 29%;
    left: -18%;
    transform: scale(0.8);
  }

  @media (max-width: 1200px) {
    top: 25%;
    left: -18%;
    transform: scale(0.8) rotate(15deg);
  }

  @media (max-width: 1200px) {
    top: 25%;
    left: -20%;
    transform: scale(0.65);
  }

  @media (max-width: 900px) {
    top: 20%;
    left: -20%;
    transform: scale(0.65);
    z-index: 8;
  }
`;
