import { Text } from "../../assets/styles/component";
import * as S from "./SIntroDesktop";
import { LazyLoadImage } from "react-lazy-load-image-component";

import Logo_img_1 from "../../assets/images/intro_Img_1.webp";
import Logo_img_2 from "../../assets/images/intro_Img_1.webp";
import Logo_img_3 from "../../assets/images/intro_img_3.webp";
import Logo_img_4 from "../../assets/images/intro_img_4.webp";
import Link from "../../assets/images/Link.svg";
import playbtn from "../../assets/images/PlayBtn.svg";
import crown from "../../assets/images/crown.svg";
import arrow from "../../assets/images/intro_arrow.svg";
import { useEffect, useState } from "react";

const Intro = ({ language }) => {
  const [show, setShow] = useState(false);
  const [show2, setShow2] = useState(false);

  const handleScroll = () => {
    if (window.scrollY >= 400) setShow(true);
    if (window.scrollY >= 1400) setShow2(true);

    if (window.scrollY <= 400) setShow(false);
    if (window.scrollY <= 1400) setShow2(false);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <S.SIntro>
      <S.Title>
        <S.BlueBG>AR интеграции</S.BlueBG> продуктов <br /> и брендов
        <S.PinkBG>
          <svg
            width="64"
            height="40"
            viewBox="0 0 64 40"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M0 0C13 25.999 34 38.999 64 40H0V0Z"
              fill="url(#paint0_linear_215_2)"
            />
            <defs>
              <linearGradient
                id="paint0_linear_215_2"
                x1="0"
                y1="0"
                x2="28"
                y2="40"
                gradientUnits="userSpaceOnUse"
              >
                <stop stopColor="#D85599" />
                <stop offset="1" stopColor="#D64B93" />
              </linearGradient>
            </defs>
          </svg>
          в стримы
        </S.PinkBG>
      </S.Title>

      <Text size="20">
        Ваша реклама становится более заметной и эффективной
      </Text>

      <S.FrameBoxContainer>
        <S.FrameBox show={show}>
          <S.BlurCard>
            <div>
              <small>Просмотры</small>
              <Text>2.1 млн</Text>
            </div>
            <div>
              <small>Переходы</small>
              <Text>63k (3%)</Text>
            </div>
          </S.BlurCard>

          <S.Frame>
            <S.BackImg src={Logo_img_1} alt="" />
            <S.FrontImg src={Logo_img_2} alt="" />
          </S.Frame>

          <S.PlayBtn src={playbtn} alt="Play Button" />

          <S.Promo>
            <div>
              <small>промокод</small>
              <Text>OLYUSHKA</Text>
            </div>
            <img src={Link} alt="Промокод" />
          </S.Promo>
        </S.FrameBox>
      </S.FrameBoxContainer>

      <S.SubTitle>Что мы предлагаем</S.SubTitle>
      <Text size="20">
        Объединенные технологии AR и AI позволяют создавать <br /> уникальный
        контент прямо на трансляции стримера
      </Text>

      <S.FrameBoxContainer>
        <S.FrameBox show2={show2}>
          <S.BlurCard>
            <div>
              <small>Просмотры</small>
              <Text>2.1 млн</Text>
            </div>
            <div>
              <small>Переходы</small>
              <Text>63k (3%)</Text>
            </div>
          </S.BlurCard>

          <S.Arrow src={arrow} alt="Arrow" loading="lazy" />

          <S.Tag color="blue">
            <svg
              width="29"
              height="19"
              viewBox="0 0 29 19"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M0 0C8 10 11 16.5 29 19H0V0Z"
                fill="url(#paint0_linear_246_2)"
              />
              <defs>
                <linearGradient
                  id="paint0_linear_246_2"
                  x1="14.5"
                  y1="0"
                  x2="14.5"
                  y2="19"
                  gradientUnits="userSpaceOnUse"
                >
                  <stop stopColor="#6C7AF4" />
                  <stop offset="1" stopColor="#5867F3" />
                </linearGradient>
              </defs>
            </svg>
            лояльная аудитория
          </S.Tag>
          <S.Tag color="yellow">
            <svg
              width="30"
              height="19"
              viewBox="0 0 30 19"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M0 18.9995C11.5 18.9995 24.5 12 30 -0.000488281V18.9995H0Z"
                fill="url(#paint0_linear_247_2)"
              />
              <defs>
                <linearGradient
                  id="paint0_linear_247_2"
                  x1="15"
                  y1="-0.000488281"
                  x2="15"
                  y2="18.9995"
                  gradientUnits="userSpaceOnUse"
                >
                  <stop stopColor="#FFB545" />
                  <stop offset="1" stopColor="#FFAB2C" />
                </linearGradient>
              </defs>
            </svg>
            AI технологии
          </S.Tag>

          <S.FrameText>
            <LazyLoadImage src={crown} alt="Play Button" loading="lazy" />
            <Text>
              Повышение результативности охватных и perfomance кампаний
            </Text>
          </S.FrameText>

          <S.Frame>
            <S.BackImgLazy src={Logo_img_3} alt="" loading="lazy" />
            <S.FrontImgLazy src={Logo_img_4} alt="" loading="lazy" />
          </S.Frame>

          <S.Promo>
            <div>
              <small>промокод</small>
              <Text>OLYUSHKA</Text>
            </div>
            <LazyLoadImage src={Link} alt="Промокод" loading="lazy" />
          </S.Promo>
        </S.FrameBox>
      </S.FrameBoxContainer>
    </S.SIntro>
  );
};

export default Intro;
